@media screen and (max-width: 1390px) {
    .contacts-image {
        padding-right: 40px;
    }
    .container {
        overflow: hidden;
    }
    .contacts {
        overflow: hidden;
    }
}

@media screen and (max-width: 1315px) {
    .container {
        width: 1140px;
    }
    .card-item {
        width: 275px;
    }
    .portfolio-item {
        width: 370px;
    }
}

@media screen and (max-width: 1170px) {
    .container {
        width: 1024px;
    }
    .about-wrapper {
        justify-content: space-around;
    }
    .about-image {
        margin-right: 0;
    }
    .about-content {
        width: 450px;
    }
    .progress-bar .progress-bar__line {
        width: 465px !important;
    }
    .services-box .services-item {
        width: 333px;
    }
    .card-item {
        width: 250px;
    }
    .works-box {
        justify-content: space-between;
    }
    .portfolio-wrapper {
        justify-content: space-around;
    }
    .portfolio-item {
        width: 420px;
        margin-bottom: 55px;
    }
    .contacts-image {
        padding-right: 50px;
    }
    .contacts-image img {
        width: 360px;
    }
}

@media screen and (max-width: 1060px) {
    .container {
        width: 960px;
    }
    .progress-bar .progress-bar__line {
        width: 440px !important;
    }
    .works-box {
        justify-content: center;
    }
    .services-box .services-item {
        width: 310px;
        padding: 15px 5px;
    }
    .card-item {
        width: 350px;
        margin: 0px 20px 50px;
    }
    .works .works-link {
        margin: 0 auto;
    }
    .contacts-image {
        padding-right: 40px;
    }
    .contacts-image img {
        width: 320px;
    }
}

@media screen and (max-width: 990px) {
    .container {
        width: 730px;
    }
    .header-up {
        background-position: center left;
    }
    .parallax-letters .parallax__item:first-child {
        margin-top: -35px;
        margin-right: -45px;
    }
    .about-image img {
        width: 240px;
    }
    .about-image .img-border {
        top: 20px;
        right: -35px;
    }
    .about-wrapper {
        justify-content: space-between;
    }
    .about-content {
        width: 390px;
    }
    .about-content .about-content__text {
        font-size: 16px;
    }
    .title {
        font-size: 36px;
    }
    .progress-bar {
        width: 85%;
    }
    .progress-bar .progress-bar__line {
        width: 100% !important;
    }
    .skills {
        padding: 60px 0px;
    }
    .services-box {
        flex-direction: column;
    }
    .services-box .services-item {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    .services {
        padding: 60px 0px;
    }
    .portfolio {
        padding: 60px 0px;
    }
    .card-item {
        width: 290px;
    }
    .portfolio-item {
        width: 335px;
        margin-bottom: 30px;
    }
    .contacts-media .contacts-media__text {
        font-size: 16px;
        width: 445px;
    }
    .contacts-media .contacts-media__item {
        font-size: 18px;
    }
    .contacts-media .contacts-media__item span {
        font-size: 20px;
    }
    .contacts-image {
        position: absolute;
        top: 150px;
        right: 0;
        padding-right: 15px;
    }
    .contacts-image img {
        width: 265px;
        height: 370px;
        transform: none;
    }
    .contacts-wrapper {
        display: block;
    }
}

@media screen and (max-width: 760px) {
    .container {
        width: 540px;
    }
    .parallax-text {
        font-size: 40px;
    }
    .parallax__item {
        img {
            width: 50px;
        }
    }
    .parallax-letters {
        padding: 30px 0px 10px;
    }
    .parallax-letters .parallax__item:first-child {
        margin-top: -30px;
        margin-right: -32px;
    }
    .logo a {
        font-size: 12px;
        img {
            width: 30px;
        }
    }
    .messenger .messenger-item img {
        width: 25px;
    }
    .messenger-text {
        font-size: 13px;
    }
    .messenger .messenger-item:last-child {
        margin: 0px 55px 0px 10px;
    }
    .messenger-text {
        margin-right: 30px;
        img {
            width: 20px;
            margin-left: 20px;
        }
    }
    .menu-wrapper {
        padding: 33vh 0px;
    }
    .menu-wrapper li {
        margin: 15px 0px;
        a {
            span {
                font-size: 26px;
            }
        }
    }
    .arrow-bottom img {
        width: 20px;
    }
    .about {
        padding: 70px 0px;
    }
    .about-content {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        h1 {
            text-align: center;
            span {
                b {
                    font-size: 34px !important;
                }
            }
        }
    }
    .about-image {
        display: none;
    }
    .about-wrapper {
        flex-direction: column-reverse;
    }
    .skills .title .cd-words-wrapper b {
        font-size: 34px;
    }
    .skills {
        padding: 35px 0px;
    }
    .progress-bar .progress-bar__name {
        font-size: 18px;
    }
    .second-title {
        font-size: 26px;
    }
    .title {
        font-size: 34px;
    }
    .works {
        padding: 60px 0px;
    }
    .works-box {
        justify-content: space-around;
    }
    .portfolio {
        padding: 35px 0px;
    }
    .card-item {
        width: 235px;
        margin: 0 0 30px;
    }
    .page__parallax-window .parallax-text {
        font-size: 50px;
    }
    .portfolio-item {
        width: 255px;
        margin-bottom: 20px;
    }
    .contacts {
        padding: 50px 0px;
    }
    .contacts-wrapper {
        flex-direction: column;
    }
    .contacts-image {
        display: none;
    }
    .contacts-media .contacts-media__item {
        margin-bottom: 20px;
    }
}

@media screen and (max-width: 570px) {
    .container {
        width: 380px;
    }
    .header-up {
        background: #F2F2F2;
    }
    .header-up__box {
        flex-direction: column;
    }
    .navigtion {
        margin-top: 15px;
    }
    .about-content {
        width: 100%;
    }
    .skills-progress {
        padding-top: 35px;
    }
    .progress-bar {
        width: 95%;
    }
    .services-box {
        margin-top: 40px;
    }
    .card-item {
        width: 275px;
    }
    .works .works-link {
        width: 305px;
        font-size: 22px;
    }
    .scrollup {
        bottom: 45px;
        right: 30px;
    }
    .scrollup img {
        width: 45px;
    }
    .portfolio-item {
        width: 330px;
    }
    .contacts-media .contacts-media__text {
        width: 100%;
    }
}

@media screen and (max-width: 400px) {
    .container {
        width: 350px;
    }
    .skills .title .cd-words-wrapper b {
        font-size: 28px;
    }
    .progress-bar__count {
        width: 100%;
    }
    .progress-bar .progress-bar__name {
        font-size: 16px;
    }
    .progress-bar:last-child {
        margin-bottom: 0;
    }
    .title {
        font-size: 26px;
    }
    .footer-social {
        width: 215px;
    }
    .messenger .messenger-item:last-child {
        margin: 0;
        margin-left: 10px;
    }
    .burger-menu {
        top: 35%;
    }
    .about {
        padding: 90px 0px 70px;
    }
    .scrollbar,
    .scrollpath {
        width: 5px;
    }
    .portfolio-item {
        width: 85%;
    }
    .contacts-wrapper {
        display: block;
        text-align: center;
    }
    .second-title {
        text-align: center;
    }
    .contacts-media .contacts-media__item {
        text-align: center;
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;
        &:last-child {
            margin-bottom: 0;
        }
        span {
            margin-bottom: 5px;
        }
    }
}

@media screen and (max-width: 359px) {
    .container {
        width: 300px;
    }
    .services-box .services-item {
        width: 98%;
    }
    .works .works-link {
        width: 260px;
        font-size: 18px;
    }
    .works {
        padding: 40px 0px;
    }
}